import '../App.css';
import { Outlet } from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Navbar, Nav} from 'react-bootstrap';
import { useAuth } from "../context/authContext"

const Landing= ()=> {
    const {isAuthenticated, logout, isAdmin} = useAuth();

    return (
        <div>
            <Navbar bg="dark" variant="dark">
            <Navbar.Brand href="/">CarShop</Navbar.Brand>
                {isAuthenticated && isAdmin ? (
                    <Nav className="mr-auto">
                        <Nav.Link href="/admin/review">Review Ticket</Nav.Link>
                        <Nav.Link href="/admin/stadistics">Stadistics</Nav.Link>
                        <Nav.Link href="/admin/login" onClick={()=>{logout();}}>Logout</Nav.Link>
                    </Nav>
                ): isAuthenticated ? (
                    <Nav className="mr-auto">
                        <Nav.Link href="/">Home</Nav.Link>
                        <Nav.Link href="/packages">Our Services</Nav.Link>
                        <Nav.Link href="/ticket">Get Ticket</Nav.Link>
                        <Nav.Link href="/history">My Tickets</Nav.Link>
                        <Nav.Link href="/home" onClick={()=>{logout();}}>Logout</Nav.Link>
                    </Nav>
                ):(
                    <Nav className="mr-auto">
                        <Nav.Link href="/">Home</Nav.Link>
                        <Nav.Link href="/packages">Our Services</Nav.Link>
                        <Nav.Link href="/register">Get Accout</Nav.Link>
                        <Nav.Link href="/login">Login</Nav.Link>
                    </Nav>
                )}
                
            </Navbar>
            <Outlet />
        </div>
      );
}

export default Landing;