import React from 'react'

export const CarForm = ({data,updateFieldHandler}) => {

    return (
        <div className='h-full flex'>
            <div className='flex-1 text-gray-700 p-20'>
                <h1>Now it's your vehicle's turn</h1>
                <p className='text-lg text-gray-500'>Enter your vehicle data to know it better</p>
                <div className='mt-6'>
                    <div className='pb-4'>
                        <label className="block font-latoBold text-sm pb-1 " htmlFor='brand'>Brand</label>
                        <input
                            className='border-2 border-gray-500 p-1 rounded-ms w-full focus:border-teal-500 focus:ring-teal-500'
                            type="text"
                            value={data.brand}
                            name="brand"
                            placeholder='Enter your vehicle brand'
                            onChange={(e)=>updateFieldHandler("brand",e.target.value)}
                            required
                        />
                    </div>
                </div>
                <div className='mt-6'>
                    <div className='pb-4'>
                        <label className="block font-latoBold text-sm pb-1 " htmlFor='model'>Model</label>
                        <input
                            className='border-2 border-gray-500 p-1 rounded-ms w-full focus:border-teal-500 focus:ring-teal-500'
                            type="text"
                            name="model"
                            value={data.model}
                            placeholder='Enter your vehicle model'
                            onChange={(e)=>updateFieldHandler("model",e.target.value)}
                            required
                        />
                    </div>
                </div>
                <div className='mt-6'>
                    <div className='pb-4'>
                        <label className="block font-latoBold text-sm pb-1" htmlFor='plate'>Plate</label>
                        <input
                            className='border-2 border-gray-500 p-1 rounded-ms w-full focus:border-teal-500 focus:ring-teal-500'
                            type="text"
                            name = "plate"
                            value={data.plate}
                            placeholder='Enter your vehicle plate'
                            onChange={(e)=>updateFieldHandler("plate",e.target.value)}
                            required
                        />
                    </div>
                </div>
                <div className='mt-6'>
                    <div className='pb-4'>
                        <label className="block font-latoBold text-sm pb-1" htmlFor='tankLevel'>Fuel tank level</label>
                        <input
                            className='border-2 border-gray-500 p-1 rounded-ms w-full focus:border-teal-500 focus:ring-teal-500'
                            type="text"
                            name = "tankLevel"
                            value={data.tankLevel}
                            placeholder='Enter your vehicle tankLevel'
                            onChange={(e)=>updateFieldHandler("tankLevel",e.target.value)}
                            required
                        />
                    </div>
                </div>
                <div className='mt-6'>
                    <div className='pb-4'>
                        <label className="block font-latoBold text-sm pb-1" htmlFor='info'>Relevant information</label>
                        <textarea
                            className='border-2 border-gray-500 p-1 rounded-ms w-full focus:border-teal-500 focus:ring-teal-500'
                            name = "info"
                            value={data.info}
                            placeholder='Enter your Relevant information'
                            onChange={(e)=>updateFieldHandler("info",e.target.value)}
                        />
                    </div>
                </div>
            </div>
        </div>
    )
};