import {Line} from 'react-chartjs-2';
import {Chart as ChartJS,CategoryScale,LinearScale,PointElement,LineElement,Title,Tooltip,Legend,Filler} from 'chart.js'
ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
    Filler
);


export default function LineChart({tickets}){
    const ticketsPorMes = {};
    tickets.forEach((ticket) => {
      const mes = new Date(ticket.services.date).getMonth() + 1;
      if (!ticketsPorMes[mes]) {
        ticketsPorMes[mes] = 0;
      }
    
      ticketsPorMes[mes]++;
    });
    const months = Object.keys(ticketsPorMes);
    const number = Object.values(ticketsPorMes);
    function obtenerNombreMes(numeroMes) {
        const meses = [
            'January',
            'February',
            'March',
            'April',
            'May',
            'June',
            'July',
            'August',
            'September',
            'October',
            'November',
            'December',
          ];
        if (numeroMes >= 1 && numeroMes <= 12) {
            return meses[numeroMes - 1];
          } else {
            return 'Mes invlido';
          }
      }
    
    const months_name =  months.map((numeroMes) => obtenerNombreMes(numeroMes));


    const data = {
        labels: months_name,
        datasets: [
          {
            label: 'Tickets',
            data: number,
            borderColor: 'rgb(75, 192, 192)',
            fill: false,
          },
        ],
      };
      

    var myoptions = {
    }
    return <Line data={data} options={myoptions}/>
}