import { useForm } from "react-hook-form"
import { useAuth } from "../context/authContext"
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";


function Register() {
    const { register, handleSubmit, formState: { errors } } = useForm();
    const { singup, isAuthenticated, errors:registerError } = useAuth();
    const navigation = useNavigate();

    useEffect(() => {
        if (isAuthenticated) {
            navigation("/home")
        }
    }, [isAuthenticated])

    const onSubmit = handleSubmit(async (values) => {
        singup(values);
    })

    return (
        <div className="py-5 h-400 w-full">
            <div className="row d-flex justify-content-center align-items-center h-100">
                <div className="col-12 col-md-8 col-lg-6 col-xl-5">
                    <div className="card bg-dark text-white">
                        <div className="card-body p-5 text-center">
                            <form onSubmit={onSubmit}>
                                <div className="mb-md-5 mt-md-4 pb-5">

                                    <h2 className="fw-bold mb-2 text-uppercase">Get your account</h2>
                                    <p className="text-white-50 mb-5">Please enter your information</p>
                                    {registerError.map((error,i)=>(
                                        <div className="text-red-500 p-2" key={i}>
                                            {error}
                                        </div>
                                        ))
                                    }
                                    <div className="form-outline form-white mb-4">
                                        <input type="text" {...register("first_name", { required: true })}
                                            id="first_name"
                                            className="w-full bg-zinc-700 text-white px-4 py-2 rounded-md my-3"
                                            placeholder="Your Name"
                                        />
                                        {
                                            errors.first_name && (
                                                <p className="text-red-500">Name is required</p>
                                            )
                                        }
                                        <label className="form-label" htmlFor="first_name">First Name</label>
                                    </div>

                                    <div className="form-outline form-white mb-4">
                                        <input type="text" {...register("last_name", { required: true })}
                                            id="last_name"
                                            className="w-full bg-zinc-700 text-white px-4 py-2 rounded-md my-3"
                                            placeholder="Your Last Name"
                                        />
                                        {
                                            errors.last_name && (
                                                <p className="text-red-500">Lastname is required</p>
                                            )
                                        }
                                        <label className="form-label" htmlFor="last_name">Last Name</label>
                                    </div>

                                    <div className="form-outline form-white mb-4">
                                        <input type="email" {...register("email", { required: true })}
                                            className="w-full bg-zinc-700 text-white px-4 py-2 rounded-md my-3"
                                            placeholder="email@email.com"
                                        />
                                        {
                                            errors.email && (
                                                <p className="text-red-500">Email is required</p>
                                            )
                                        }                                        
                                        <label className="form-label" htmlFor="email">Email</label>
                                    </div>

                                    <div className="form-outline form-white mb-4">
                                        <input type="password" {...register("password", { required: true })}
                                            className="w-full bg-zinc-700 text-white px-4 py-2 rounded-md my-3"
                                            placeholder="Your Password"
                                        />
                                        {
                                            errors.password && (
                                                <p className="text-red-500">Password is required</p>
                                            )
                                        }                                        
                                        <label className="form-label" htmlFor="password">Password</label>
                                    </div>


                                    <button type="submit" className="btn btn-outline-light btn-lg px-5">Register</button>

                                    <div className="d-flex justify-content-center text-center mt-4 pt-1">
                                        <a href="#!" className="text-white"><i className="fab fa-facebook-f fa-lg"></i></a>
                                        <a href="#!" className="text-white"><i className="fab fa-twitter fa-lg mx-4 px-2"></i></a>
                                        <a href="#!" className="text-white"><i className="fab fa-google fa-lg"></i></a>
                                    </div>
                                    <p className="mb-0">Have an account? <a href="/login" className="text-white-50 fw-bold">Login</a></p>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}


export default Register;