import React from 'react'

export const Form = ({data,updateFieldHandler}) => {


return (
        <div className='h-full flex '>
            <div className='flex-1 text-gray-700 p-20'>
                <h1>Let's get started with you!</h1>
                <p className='text-lg text-gray-500'>Enter your personal data to agend the apointment</p>
                <div className='mt-6'>
                    <div className='pb-4'>
                        <label className="block font-latoBold text-sm pb-1 " htmlFor='firs_name'>Name:</label>
                        <input
                            className='border-2 border-gray-500 p-1 w-full rounded-ms focus:border-teal-500 focus:ring-teal-500'
                            type="text"
                            name="firs_name"
                            value={data.firs_name || ""}
                            placeholder='John'
                            required
                            onChange={(e)=>updateFieldHandler("firs_name",e.target.value)}
                        />
                    </div>
                </div>
                <div className='mt-6'>
                    <div className='pb-4'>
                        <label className="block font-latoBold text-sm pb-1 " htmlFor='last_name'>Last Name:</label>
                        <input
                            className='border-2 border-gray-500 p-1 w-full rounded-ms focus:border-teal-500 focus:ring-teal-500'
                            type="text"
                            value={data.last_name || ""}
                            name="name"
                            placeholder='Smith'
                            required                            
                            onChange={(e)=>updateFieldHandler("last_name",e.target.value)}
                        />
                    </div>
                </div>
                <div className='mt-6'>
                    <div className='pb-4'>
                        <label className="block font-latoBold text-sm pb-1 " htmlFor='email'>Email</label>
                        <input
                            className='border-2 border-gray-500 p-1 w-full rounded-ms focus:border-teal-500 focus:ring-teal-500'
                            type="email"
                            name="email"
                            value={data.email || ""}
                            placeholder='example@example.com'
                            required
                            onChange={(e)=>updateFieldHandler("email",e.target.value)}
                        />
                    </div>
                </div>
                <div className='mt-6'>
                    <div className='pb-4'>
                        <label className="block font-latoBold text-sm pb-1" htmlFor='phone'>Phone Number</label>
                        <input
                            className='border-2 border-gray-500 p-1 w-full rounded-ms focus:border-teal-500 focus:ring-teal-500'
                            type="text"
                            name = "phone"
                            value={data.phone}
                            placeholder='+1 236545265'
                            required
                            onChange={(e)=>updateFieldHandler("phone",e.target.value)}
                        />
                    </div>
                </div>
                <div className='mt-6' >
                    <p className='text-lg text-gray-500 '>Select your type of identification</p>
                    <div className='pb-4  border-2 '>
                        <label className="font-latoBold text-sm p-2"> 
                            <input 
                                className='border-2 border-gray-500 p-2 '
                                type="radio"
                                name="options"
                                value="C.I"
                                checked={data.type_id === "C.I"}
                                onChange={(e)=>updateFieldHandler("type_id",e.target.value)}
                            />Identification Card
                        </label>
                        <label className="font-latoBold text-sm p-2"> 
                            <input 
                                className='border-2 border-gray-500 p-2  '
                                type="radio"
                                name="options"
                                value="RUC"
                                checked={data.type_id === "RUC"}
                                onChange={(e)=>updateFieldHandler("type_id",e.target.value)}
                            />RUC
                        </label>
                        <label className="font-latoBold text-sm p-2"> 
                            <input 
                                className='border-2 border-gray-500 p-2 gap-2 '
                                type="radio"
                                name="options"
                                value="Passaport"
                                checked={data.type_id === "Passaport"}
                                onChange={(e)=>updateFieldHandler("type_id",e.target.value)}
                            />Passaport
                        </label>
                        <div className='pb-4' style={{padding:"10px"}}>
                            <label className="block font-latoBold text-sm pb-1 " htmlFor='id_number'>Identification Number</label>
                            <input
                                className='border-2 border-gray-500 p-2 rounded-ms w-100 focus:border-teal-500 focus:ring-teal-500'
                                type="text"
                                name="id_number"
                                value={data.id_number}
                                placeholder='123456789'
                                onChange={(e)=>updateFieldHandler("id_number",e.target.value)}
                            />
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
};