import './App.css';
import {Routes,Route} from "react-router-dom";
import Home from "./pages/Home";
import Packages from "./pages/Packages";
import Landing from './components/landing';
import { AuthProvider } from './context/authContext';
import Register from './pages/Register';
import Ticket from "./pages/Ticket";
import Schedule from "./components/Schedule"
import TicketReview from './pages/TicketReview';
import Stadistics from './pages/Stadistic'
import Login from "./pages/Login"
import AdminLogin from "./pages/AdmiLogin"
import History from "./pages/History"
import ProtectedRoute from './ProtectedRoute';
import AdminRoute from './AdminRoute';
import { TicketProvider } from './context/ticketContext';

function App() {

  return (
    <div>
      <AuthProvider>
        <TicketProvider>
        <Routes>
          <Route path="/" element={<Landing></Landing>}>
            <Route path="/" element={<Home></Home>}/>
            <Route path="*" element={<Home></Home>}/>
            <Route path="login" element={<Login></Login>}/>
            <Route path="admin/login" element={<AdminLogin></AdminLogin>}/>
            <Route path="register" element={<Register></Register>}/>
            <Route path="packages" element={<Packages></Packages>}/>
            <Route element={<ProtectedRoute/>}>
              <Route path="ticket" element={<Ticket></Ticket>}/>
              <Route path="ticket/:id" element={<Ticket></Ticket>}/>
              <Route path="history" element={<History></History>}/>
            </Route>
            <Route element={<AdminRoute/>}>
              <Route path="admin/review" element={<TicketReview></TicketReview>}/>
              <Route path="admin/review/ticket/:id" element={<Schedule></Schedule>}/>
              <Route path='admin/stadistics' element={<Stadistics></Stadistics>}/>
            </Route>
          </Route>
        </Routes>
        </TicketProvider>
      </AuthProvider>
    </div>
  );
}

export default App;
