import { Table} from 'react-bootstrap';
import { useTicket} from '../context/ticketContext';
import { useAuth} from '../context/authContext';
import Schedule from "../components/Schedule"
import { useEffect, useState} from 'react';
import { AiFillDelete } from "react-icons/ai";

function TicketReview() {

    const { getTickets, tickets } = useTicket([]);
    const [modalOpen, setModalOpen] = useState(false);
    const { user } = useAuth();
    const [ticket_id,setTicketId] = useState("")
    const [action,setAction] = useState("")
  
    useEffect(()=>{
      getTickets()
    },[])

    if(tickets.length === 0) return (<h1>No Task</h1>)

    return (
      <div className='mx-4 my-2'>
        {modalOpen && <Schedule setOpenModal={setModalOpen} params={ticket_id} action ={action}/>}
        <Table striped bordered hover responsive="md">
          <thead>
            <tr>
              <th style={{ width: '15%' }}>Ticket ID</th>
              <th style={{ width: '10%' }}>Customer</th>
              <th style={{ width: '10%' }}>Brand</th>
              <th style={{ width: '10%' }}>Date</th>
              <th style={{ width: '10%' }}>Time</th>
              <th style={{ width: '10%' }}>Status</th>
              <th style={{ width: '14%' }}></th>
            </tr>
          </thead>
          <tbody>
            {tickets.map((ticket) => (
            <tr key={ticket.id}>
                <td style={{ width: '15%' }}>{ticket.id}</td>
                <td style={{ width: '10%' }}>{ticket.firs_name} {ticket.last_name}</td>
                <td style={{ width: '10%' }}>{ticket.brand}</td>
                <td style={{ width: '10%' }}>{ticket.services.date}</td>
                <td style={{ width: '10%' }}>{ticket.services.time}</td>
                <td style={{ width: '10%' }}>{ticket.status}</td>
                <td style={{ width: '14%' }}>
                  <div className='flex items-center'>
                  <button className="bg-indigo-500 hover:bg-indigo-950 rounded px-2 py-1 mx-2 rounded-sm text-white" 
                  onClick={() => {
                                  setModalOpen(true);
                                  setTicketId(ticket.id);
                                  setAction("Schedule")
                                }}>Schedule</button>
                  
                  {(user.role==="admin")&&<button className="bg-red-500 hover:bg-red-950 rounded px-2 py-1 my-1 rounded-sm text-white"
                  onClick={() => {
                    setModalOpen(true);
                    setTicketId(ticket.id);
                    setAction("Cancel")
                  }}>Cancel</button>}
                  {(user.role==="admin")&&<button className="bg-red-950 hover:bg-stone-950 rounded px-2 py-2 my-1 mx-2 rounded-sm text-white"
                  onClick={() => {
                    setModalOpen(true);
                    setTicketId(ticket.id);
                    setAction("Delete")
                  }}><AiFillDelete/></button>}
                  </div>
                </td>
          </tr>
        ))}
          </tbody>
        </Table>
        </div>
      );
}

export default TicketReview;