import axios from "./axios";


export const requestRegister = user => axios.post(`/api/register`,user);
export const requestLogin = user => axios.post(`/api/login`,user);
export const requestLogout = () => axios.get(`/api/logout`);
export const verifyTokenRequest = () => axios.get('/admin/verify');
export const loginRequest = user => axios.post(`/admin/login`,user);

export const logoutRequest = () => axios.get(`/admin/logout`);