import LineChart from "../components/Chart";
import { useTicket} from '../context/ticketContext';
import { useEffect} from 'react';
function Stadistics() {
    const { getTickets, tickets } = useTicket([]);
    useEffect(()=>{
        getTickets()
      },[])
    return(
        <div>
            <h1 className="bg-info text-center font-monospace fw-bold lh-base">Tickets by month</h1>
            <div>
                <p className="m-2">Tickets Schedule</p>
                <div className="ng-light mx-auto px-2 border border-2 proder-primary"
                style={{width:"1000px",height:"500px"}}>
                    <LineChart tickets={tickets}></LineChart>
                </div>
            </div>
        </div>
    );
}

export default Stadistics;