import axios from "./axios";


export const getTicketsRequest = () => axios.get('admin/');
export const getTicketbyUserRequest = () => axios.get(`api/tickets`);
export const getTicketRequest = (id) => axios.get(`api/ticket/${id}`);
export const createTicketRequest = (ticket) => axios.post('api/ticket',ticket);
export const updateTicketsRequest = (id,ticket) => axios.put(`api/ticket/${id}`,ticket);
export const updateAdminTicketsRequest = (id,ticket) => axios.put(`admin/ticket/${id}`,ticket);
export const deleteTicketRequest = (id) => axios.delete(`admin/ticket/${id}`)
