import React from 'react'



export const Review = ({data}) => {
return (
    <div className='h-screen flex items-center justify-center w-full'>
        <div className='flex-1 text-gray-700 p-20'>
            <h1>Preview Ticket</h1>
            <div className="border-t border-gray-100">
                <dl className="divide-y divide-gray-100">
                <div className="px-4 py-6 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6" >
                    <dt className="text-lg font-medium text-gray-900">Customer name</dt>
                    <dd className="mt-1 text-lg leading-6 text-gray-700 sm:col-span-2 sm:mt-0">{data.firs_name} {data.last_name}</dd>
                </div>
                <div className="px-4 py-6 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
                    <dt className="text-lg font-medium text-gray-900">Vehicle information</dt>
                    <dd className="mt-1 text-lg leading-6 text-gray-700 sm:col-span-2 sm:mt-0">{data.brand} - {data.model}</dd>
                </div>
                <div className="px-4 py-6 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
                    <dt className="text-lg font-medium text-gray-900">Email address</dt>
                    <dd className="mt-1 text-lg leading-6 text-gray-700 sm:col-span-2 sm:mt-0">{data.email}</dd>
                </div>
                <div className="px-4 py-6 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
                    <dt className="text-lg font-medium text-gray-900">Working time</dt>
                    <dd className="mt-1 text-lg leading-6 text-gray-700 sm:col-span-2 sm:mt-0">{(data.services.worktime)/60} hours</dd>
                </div>
                <div className="px-4 py-6 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
                    <dt className="text-lg font-medium text-gray-900">Delivery date and time</dt>
                    <dd className="mt-1 text-lg leading-6 text-gray-700 sm:col-span-2 sm:mt-0">{data.services.date} - {data.services.time}</dd>
                </div>
                <div className="px-4 py-6 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
                    <dt className="text-lg font-medium text-gray-900">Services</dt>
                    <ul className="mt-1 text-lg leading-6 text-gray-700 sm:col-span-2 sm:mt-0">
                        {data.services.services.map((e,i) => (
                            <li key={i}>{e}</li>
                        ))}
                    </ul>
                </div>
                </dl>
            </div>
        </div>
    </div>
  )
}

export default Review;