import { useTicket} from '../context/ticketContext';
import { useForm } from "react-hook-form"
import "../features/style/Modal.css"

function Schedule({ setOpenModal , params, action}) {
  
  return (
    <div className="modalBackground">
      <div className="modalContainer">
        <div className="titleCloseBtn">
          <button
            onClick={() => {
              setOpenModal(false);
            }}
          >
            X
          </button>
        </div>
        {(action==="Schedule") && <ScheduleDate params={params} setOpenModal={setOpenModal}/>}
        {(action==="Cancel") && <CancelDate params={params} setOpenModal={setOpenModal}/>}
        {(action==="Delete") && <DeleteDate params={params} setOpenModal={setOpenModal}/>}
    
      </div>
    </div>
  );
}

function ScheduleDate({params,setOpenModal}){
  const {tickets,updateDateTicket} = useTicket();
  function findTicketById(tickets, targetId) {
    return tickets.find((ticket) => ticket.id === targetId);
  }
  const selectedTicket = findTicketById(tickets,params)

  const { register, handleSubmit} = useForm();
  
  const onSubmit = handleSubmit(async (values)=>{
    const updatedTicket = { ...selectedTicket };
    updatedTicket.services.date = values.date;
    updatedTicket.services.time = values.time;
    updatedTicket.status = "Schedule"
    updateDateTicket(params,updatedTicket);
    setOpenModal(false);
    window.location.reload();
  })
  return(
      <div className="h-1/2">
        <div className="title">
          <h2>Set the Date and time to the Appointmend</h2>
        </div>
        <div className="body">
          <form onSubmit={onSubmit}>
            <div className="">
            <div className="form-outline form-white mb-1">
                <input type="date" {...register("date", { required: true })}
                    className="w-full bg-zinc-700 text-white px-4 py-2 rounded-md my-3"
                />                                
                <label className="form-label" htmlFor="date">Date</label>
            </div>
            <div className="form-outline form-white mb-1">
                <input type="time" {...register("time", { required: true })}
                    className="w-full bg-zinc-700 text-white px-4 py-2 rounded-md my-3"
                />                                
                <label className="form-label" htmlFor="time">Time</label>
            </div>
            <button className='bg-red-500 hover:bg-red-950 rounded px-2 py-1 my-1 rounded-sm text-white'
            onClick={() => {setOpenModal(false);}} id="cancelBtn">Cancel</button>
            <button type="submit" className="bg-indigo-500 hover:bg-indigo-950 rounded px-2 py-1 mx-2 rounded-sm text-white">Schedule</button>
            </div>
          </form>
        </div>
    </div>
  );
}

function CancelDate({params,setOpenModal}){
  const {tickets,updateDateTicket} = useTicket();
  function findTicketById(tickets, targetId) {
    return tickets.find((ticket) => ticket.id === targetId);
  }
  const selectedTicket = findTicketById(tickets,params)
  const { register, handleSubmit} = useForm();

  const onSubmit = handleSubmit(async (values)=>{
    const updatedTicket = { ...selectedTicket };
    updatedTicket.services.date = values.motive;
    updatedTicket.status = "Canceled";
    updateDateTicket(params,updatedTicket);
    setOpenModal(false);
    window.location.reload();
  })
  return(
    <div>
      <div className="title">
          <h1>Set the Motive to cancelation</h1>
      </div>
      <div className="body h-auto">
        <form onSubmit={onSubmit}>
          <div className="mb-md-2 mt-md-4 pb-5">
          <h2 className="fw-bold mb-2 text-white text-uppercase">Select Apointment Date</h2>
          <div className="form-outline form-white mb-4">
              <input type="text" {...register("motive", { required: true })}
                  className="w-full bg-zinc-700 text-white px-4 py-2 rounded-md my-3"/>                                
              <label className="form-label text-white" tmlFor="motive">motive of the cancelation</label>
          </div>
          <button className='bg-slate-400 hover:bg-slate-800 rounded px-2 py-1 my-1 mx-2 rounded-sm text-white'
          onClick={() => {setOpenModal(false);}} >Back</button>
          <button type="submit" className="bg-red-500 hover:bg-red-950 rounded px-2 py-1 my-1 rounded-sm text-white">Cancel</button>
          </div>
        </form>
      </div>
    </div>
  );
}

function DeleteDate({params,setOpenModal}){
  const {tickets,deleteTicket} = useTicket();
  function findTicketById(tickets, targetId) {
    return tickets.find((ticket) => ticket.id === targetId);
  }
  const selectedTicket = findTicketById(tickets,params)
  return(
    <div className='h-auto'>
        <div className="title">
          <h1>Are you sure of delete the Ticket?</h1>
        </div>
        <div className="body my-5 ">
          <button className='bg-slate-400 hover:bg-slate-800 rounded px-2 py-1 my-1 mx-2 rounded-sm text-white'
          onClick={() => {setOpenModal(false);}}>Back</button>
          <button className="bg-red-500 hover:bg-red-950 rounded px-2 py-1 my-1 rounded-sm text-white"
          onClick={() => {
            deleteTicket(params);
            setOpenModal(false);
            }}>Delete</button>
          </div>
    </div>
  )
}
export default Schedule;