
export const Progress = ({currentStep}) => {

  const steps = [
    { id: 0, tag: 'Step 1', name: 'Customer Data'},
    { id: 1, tag: 'Step 2', name: 'Vehicle data'},
    { id: 2,  tag: 'Step 3', name: 'Service selection'},
    { id: 3, tag: 'Step 4', name: 'Preview'},
  ]
  
  const addStatusToSteps = (steps, currentStep) => {
    return steps.map((step) => {
      return {
        ...step,
        status: step.id === currentStep ? 'active' : step.id < currentStep ? 'complete' : 'inactive'
      };
    });
  };

  const stepsWithStatus = addStatusToSteps(steps, currentStep);

  return (
    <nav aria-label="Progress">
      <ol role="list" className="space-y-4 md:flex md:space-x-8 md:space-y-0">
        {stepsWithStatus.map((step) => (
          <li key={step.name} className="md:flex-1">
            {step.status === 'complete' ? (
              <a
                className="no-underline group flex flex-col border-l-4 border-green-600 py-2 pl-4 hover:border-indigo-800 md:border-l-0 md:border-t-4 md:pb-0 md:pl-0 md:pt-4"
              >
                <span className="text-sm font-medium text-indigo-600 group-hover:text-indigo-800">{step.tag}</span>
                <span className="text-sm font-medium">{step.name}</span>
              </a>
            ) : step.status === 'active' ? (
              <a
                className="no-underline flex flex-col border-l-4 border-indigo-600 py-2 pl-4 md:border-l-0 md:border-t-4 md:pb-0 md:pl-0 md:pt-4"
                aria-current="step"
              >
                <span className="text-sm font-medium text-indigo-600">{step.tag}</span>
                <span className="text-sm font-medium">{step.name}</span>
              </a>
            ) : (
              <a
                className="no-underline group flex flex-col border-l-4 border-gray-200 py-2 pl-4 hover:border-gray-300 md:border-l-0 md:border-t-4 md:pb-0 md:pl-0 md:pt-4"
              >
                <span className=" text-sm font-medium text-gray-500 group-hover:text-gray-700">{step.tag}</span>
                <span className="text-sm font-medium">{step.name}</span>
              </a>
            )}
          </li>
        ))}
      </ol>
    </nav>
  )
}