import { Form } from '../components/Form';
import { CarForm } from "../components/CarForm";
import { DynamoViewer } from "../components/ServiceList";
import { useForm } from '../features/hooks/useForms'
import { GrFormNext, GrFormPrevious } from "react-icons/gr";
import { FiSend } from "react-icons/fi"
import { Progress } from '../components/progress';
import { Review } from "../components/Review"
import { useEffect, useState } from 'react';
import { useTicket} from '../context/ticketContext';
import { useNavigate, useParams } from "react-router-dom";

const formTemplate = {
  firs_name: "",
  last_name: "",
  email: "",
  phone: "",
  type_id: "",
  id_number: "",
  brand: "",
  model: "",
  plate: "",
  tankLevel: "",
  info: "",
  services: [],
  status:"Send"
}

function Ticket() {
  const [data, setData] = useState(formTemplate);
  const {createTicket,getTicket,updateTicket} = useTicket();
  const navigation = useNavigate();
  const params = useParams()

  useEffect(()=>{
    async function loadTicket(){
    if(params.id){
      const ticket = await getTicket(params.id);
      setData(ticket)
    }
   }
   loadTicket();
  },[])

  const updateFieldHandler=(key,value) =>{
      setData((prev)=>{
      return {...prev, [key]:value}
    })
  }

  const formComponents = [
    <Form data={data} updateFieldHandler={updateFieldHandler}/>, 
    <CarForm data={data} updateFieldHandler={updateFieldHandler}/>, 
    <DynamoViewer updateFieldHandler={updateFieldHandler}/>, 
    <Review data={data}/>
  ];
  const { currentStep, currentComponent, changeStep, isLastStep, isFristStep } = useForm(formComponents)

  const handleSubmit = (event) =>{
    event.preventDefault();
    if(params.id){
      updateTicket(params.id,data)
      navigation("/history")
    }else{   
      createTicket(data);
      navigation("/home")
    }

  }

  return (
    <div className='app h-screen'>
      <Progress currentStep={currentStep} />
      <div className='form-container  border-2 border-cian-500 bg-white flex rounded-lg font-latoRegural w-2/3'>
        <form className='' onSubmit={handleSubmit}>
          <div className='input-container flex'>{currentComponent}
            <div className="w-1/3 h-auto">
              <img src="https://www.lamborghini.com/sites/it-en/files/DAM/lamborghini/facelift_2019/homepage/families-gallery/2023/revuelto/revuelto_m.png"></img>
            </div>
          </div>
          <div className='actions mb-5'>
            {!isFristStep && (
              <button className="bg-teal-500 hover:bg-teal-700 text-white font-bold py-2 px-4 rounded" type='button' onClick={() => changeStep(currentStep - 1)}>
                <GrFormPrevious className="inline-block mr-2" />
                Back
              </button>
            )}
            {!isLastStep ? (
              <button className="bg-teal-500 hover:bg-teal-700 text-white font-bold mx-5 py-2 px-4 rounded" type='button' onClick={(e) => changeStep(currentStep + 1, e)}>
                <GrFormNext className="inline-block mr-2" />
                Next
              </button>
            ) : (
              <button className="bg-teal-500 hover:bg-teal-700 text-white font-bold mx-5 py-2 px-4 rounded" type='submit'>
                <FiSend className="inline-block mr-2" />
                Send
              </button>)}
          </div>
        </form>
      </div>
    </div>
  );
}

export default Ticket;