import { createContext, useState, useContext, useEffect } from "react";
import { requestRegister,requestLogin,verifyTokenRequest } from "../api/auth";
import { loginRequest,logoutRequest } from "../api/auth";
import Cookie from 'js-cookie'

export const AuthContext = createContext();
export const useAuth = () => {
    const context = useContext(AuthContext);
    if(!context){
        throw new Error("useAuth must be used within an AuthProvider");
    }

    return context
}

export const AuthProvider = ({children})=>{
    const [user, setUser] = useState(null);
    const [isAuthenticated, setIsAuthenticated] = useState(false);
    const [isAdmin, setIsAdmin] = useState(false);
    const [errors,setErrors] = useState([]);
    const [loading,setLoading] = useState(true);


    const singup = async (user) => {
        try{
            const res = await requestRegister(user)
            setUser(res.data)
            setIsAuthenticated(true);
        }catch(error){
            setErrors(error.response.data)
        }

    }

    const signin = async (user) => {
        try{
            const res = await requestLogin(user);
            setUser(res.data);
            setIsAuthenticated(true);
        }catch(error){
            if(Array.isArray(error.response.data)){
                setErrors(error.response.data)
            }
            console.log(error);
            setErrors([error.response.data.message]);
        }

    }

    const adminSignin = async (user) => {
        try{
            const res = await loginRequest(user);
            setUser(res.data);
            setIsAuthenticated(true);
            setIsAdmin(true);
        }catch(error){
            if(Array.isArray(error.response.data)){
                setErrors(error.response.data)
            }
            console.log(error);
            setErrors([error.response.data.message]);
        }

    }

    const logout = async () =>{
        await logoutRequest()
        Cookie.remove("token");
        setIsAuthenticated(false);
        setUser(null);
    }

    useEffect(()=>{
        async function checkLogin(){
            const cookie = Cookie.get();
            if(!cookie.token){
                setIsAuthenticated(false);
                setLoading(false);
                return setUser(null);
            }
            try{
                const res = await verifyTokenRequest(cookie.token);
                if(!res.data){
                    setIsAuthenticated(false);
                    setLoading(false);
                    return;
                } 
                setIsAuthenticated(true);
                if(res.data.role !== "user") {setIsAdmin(true)}
                setUser(res.data);    
                setLoading(false);

            }catch(error){
                setIsAuthenticated(false);
                setUser(null);
                setLoading(false);
            } 
        }
        checkLogin();
    },[isAdmin])

    return (
    <AuthContext.Provider 
        value={{
            singup,
            signin,
            logout,
            adminSignin,
            isAdmin,
            loading,
            user,
            isAuthenticated,
            errors,
        }}
    >
        {children}
    </AuthContext.Provider>
    );

};