import { useForm } from "react-hook-form"
import { useAuth } from "../context/authContext"
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";



function Login(){
    const { register, handleSubmit, formState: { errors } } = useForm();
    const { adminSignin, isAuthenticated, errors:authrError } = useAuth();
    const navigation = useNavigate();

    useEffect(()=>{
        if(isAuthenticated){
            navigation("/admin/review")
        }
    },[isAuthenticated])

    const onSubmit = handleSubmit(async (values)=>{
        console.log(values)
        adminSignin(values)
    })

    return(
            <div className="py-5 h-400 w-full">
                <div className="row d-flex justify-content-center align-items-center h-100">
                <div className="col-12 col-md-8 col-lg-6 col-xl-5">
                    <div className="card bg-dark text-white">
                    <div className="card-body p-5 text-center">
                        <form onSubmit={onSubmit}>
                        <div className="mb-md-2 mt-md-4 pb-5">
                        <h2 className="fw-bold mb-2 text-white text-uppercase">Employer Login</h2>
                        <p className="text-white-50 mb-5">Please enter your email and password!</p>
                        {authrError.map((error,i)=>(
                            <div className="text-red-500 p-2" key={i}>
                                {error}
                            </div>
                            ))
                        }
                        <div className="form-outline form-white mb-4">
                            <input type="email" {...register("email", { required: true })}
                                className="w-full bg-zinc-700 text-white px-4 py-2 rounded-md my-3"
                                placeholder="email@email.com"
                            />
                            {
                                errors.email && (
                                    <p className="text-red-500">Email is required</p>
                                )
                            }                                        
                            <label className="form-label" htmlFor="email">Email</label>
                        </div>

                        <div className="form-outline form-white mb-4">
                            <input type="password" {...register("password", { required: true })}
                                className="w-full bg-zinc-700 text-white px-4 py-2 rounded-md my-3"
                                placeholder="Your Password"
                            />
                            {
                                errors.password && (
                                    <p className="text-red-500">Password is required</p>
                                )
                            }                                        
                            <label className="form-label" htmlFor="password">Password</label>
                        </div>

                        <p className="small mb-5 pb-lg-2"><a className="text-white-50" href="#!">Forgot password?</a></p>

                        <button type="submit" className="btn btn-outline-light btn-lg px-5">Login</button>
                            <p className="mt-2">Don't have an account? <a href="/login" className="text-white-50 fw-bold">Sign Up</a></p>
                            <p className="mt-2">Are you a client <a href="/login" className="text-white-50 fw-bold">Sign in</a></p>
                        </div>
                        </form>
                    </div>
                    </div>
                </div>
                </div>
            </div>

    )
}


export default Login;