import { Table, Button } from 'react-bootstrap';
import { useTicket} from '../context/ticketContext';
import { useAuth } from "../context/authContext";
import { Link } from "react-router-dom";
import { useEffect } from 'react';

function History() {

    const { user } = useAuth();
    const { getTicketbyuser, tickets } = useTicket([]);


    useEffect(()=>{
        getTicketbyuser(user)
    },[])

    if(tickets.length === 0) return (<h1>No Tickets</h1>)


    return (
        <Table striped bordered hover>
          <thead>
            <tr>
              <th style={{ width: '20%' }}>Ticket ID</th>
              <th style={{ width: '16%' }}>Customer</th>
              <th style={{ width: '16%' }}>Brand</th>
              <th style={{ width: '16%' }}>Date</th>
              <th style={{ width: '16%' }}>Time</th>
              <th style={{ width: '16%' }}>Status</th>
              <th style={{ width: '16%' }}></th>
            </tr>
          </thead>
          <tbody>
            {tickets.map((ticket) => (
            <tr key={ticket.id}>
                <td style={{ width: '20%' }}>{ticket.id}</td>
                <td style={{ width: '16%' }}>{ticket.firs_name} {ticket.last_name}</td>
                <td style={{ width: '16%' }}>{ticket.brand}</td>
                <td style={{ width: '16%' }}>{ticket.services.date}</td>
                <td style={{ width: '16%' }}>{ticket.services.time}</td>
                <td style={{ color: ticket.status === "Canceled" ? 'red' : 'green' }}>{ticket.status}</td>
                <td style={{ width: '16%' }}>
                {ticket.status !== "Send" ? (
                <Button
                    variant="secondary"
                    disabled={true}
                >
                Edit
                </Button>
            ) : (
                <Link className="bg-indigo-500 px-4 py-1 rounded-sm text-white" to={`/ticket/${ticket.id}`} >Edit</Link>
                )}
                </td>
          </tr>
        ))}
          </tbody>
        </Table>
      );
}

export default History;