import { createContext, useContext, useState } from "react";
import { 
    createTicketRequest, 
    getTicketbyUserRequest, 
    getTicketRequest, 
    updateTicketsRequest, 
    getTicketsRequest,
    updateAdminTicketsRequest,
    deleteTicketRequest} from "../api/ticket";

const ticketContext = createContext();

export const useTicket = () =>{
    const context = useContext(ticketContext);
    if(!context){
        throw new Error("useTicket must be used with a TicketProvider");
    }
    return context;
};

export function TicketProvider({children}){
    const [tickets,setTicket] = useState([]);

    const createTicket = async (ticket) =>{
        const res = await createTicketRequest(ticket);
    };

    const getTicketbyuser = async () =>{
        try{
            const res = await getTicketbyUserRequest();
            setTicket(res.data)
        }catch(error){
            console.log(error)
        }

    };

    const getTickets = async () => {
        try{
            const res = await getTicketsRequest();
            setTicket(res.data);
        }catch(error){
            console.log(error);
        }

    };

    const getTicket = async (id) => {
        try{
            const res = await getTicketRequest(id);
            return res.data;
        }catch(error){
            console.log(error);
        }

    };

    const updateTicket = async (id,ticket) =>{
        try{
            await updateTicketsRequest(id,ticket)
        }catch(error){
            console.log(error)
        }
    };

    const updateDateTicket = async (id,ticket) =>{
        try{
            await updateAdminTicketsRequest(id,ticket)
        }catch(error){
            console.log(error)
        }
    };

    const deleteTicket = async(id) =>{
        try{
            const res = await deleteTicketRequest(id);
            console.log(res)
            if(res.status===200){
                setTicket(tickets.filter(ticket=>ticket.id !== id))
            }
        }catch(error){
            console.log(error)
        }

    };
    return (
    <ticketContext.Provider 
        value={{
                tickets,
                getTickets,
                createTicket,
                getTicketbyuser,
                getTicket,
                updateTicket,
                updateDateTicket,
                deleteTicket,
            }}
        >
            {children}
    </ticketContext.Provider>
    );
    
}