import React from 'react'
import { useState, useEffect } from 'react';
import { getServicesRequest } from '../api/services';



export const DynamoViewer = ({updateFieldHandler}) => {
  const [service,setService] = useState([]);
  const [selected,setSelected] = useState([]);
  const currentDate = new Date();

  const getServices = async () =>{
    try{
      const res = await getServicesRequest()
      setService(res.data)
      return res.data;
    }catch(error){
      console.log(error)
    }
  }

  useEffect(()=>{
    getServices()
  },[])

  const handleCheckboxChange = (event) => {
    const {name,value} = event.target;
    setSelected((prevState) => ({
      ...prevState,
      [name]: value
    }));
  };

  const handleOnblur = () =>{
    const selectedValues = Object.keys(selected).filter((key) => selected[key]);
    const selectedTime = Object.values(selected).map((value) => parseInt(value))
    const totalTime = selectedTime.reduce((accumulator, currentValue) => accumulator + currentValue, 0);
    const dueDate = new Date(currentDate.getTime() + totalTime * 60000);
    const values = {
      services : selectedValues,
      worktime : totalTime,
      date : dueDate.toLocaleDateString(),
      time : dueDate.toLocaleTimeString()
    }
    updateFieldHandler("services",values)
  }

  
 
  return (
    <div className='dynamoContainer h-full flex items-center justify-center'>
      <div className="space-y-5">
          <h1>Choose your services</h1>
          <p className='text-lg text-gray-500'>Select one or more of our services available for your car</p>
          <hr />
          {
          service.map((element, index) => (
              <div className="relative flex items-start" key={index}>
                  <div className="flex h-6 items-center">
                      <input
                      id="coment"
                      aria-describedby="comments-description"
                      name={element.name}
                      value = {element.time}
                      onChange={handleCheckboxChange}
                      onBlur={handleOnblur}
                      type="checkbox"
                      className="h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-600"
                      />
                  </div>
                  <div className="ml-3 text-sm leading-6">
                      <label htmlFor="comments" className="font-medium text-gray-900">
                      {element.name}
                      </label>
                      <p id="comments-description" className="text-gray-500">
                      The estimated working time is {element.time} minutes.
                      </p>
                  </div>
              </div>
          ))}
    </div>
    </div>
    
  )
}